<template>
  <v-container>
    <p>Progress {{ year }}</p>
    <v-progress-linear
        :value="yearProgress"
        height="25"
        class="white--text"
    >
      <strong>{{ yearProgress }}%</strong>
    </v-progress-linear>
  </v-container>
</template>
<script>
export default {
  name: 'YearProgress',
  computed: {
    year() {
      return new Date().getFullYear()
    },
    yearProgress() {
      const month = new Date().getMonth()
      const date = new Date().getDate()
      const today = new Date(this.year, month, date)
      const firstDateOfYear = new Date(this.year, 0, 1)
      const lastDateOfYear = new Date(this.year + 1, 0, 1)
      const yearDiff = lastDateOfYear - firstDateOfYear
      const currentDiff = today - firstDateOfYear
      return (currentDiff * 100 / yearDiff).toFixed(2)
    }
  }
}
</script>
